 $media == xl {\n    @media (min-width: 1439px) and (max-width: 4200px) { @content; }\n  }\n}\n\n@mixin respondto($media...) {\n  @each $mediatype in $media {\n    @include respond-to($mediatype) { @content; }\n  }\n}\n\n@mixin box-shadow($shadow: 0 3px 0 rgba(0, 0, 0, 0.1)) {\n  -moz-box-shadow: $shadow;\n  -webkit-box-shadow: $shadow;\n  box-shadow: $shadow;\n}\n\n@mixin border-radius($radius) {\n  -moz-border-radius: $radius;\n  -webkit-border-radius: $radius;\n  border-radius: $radius;\n}\n","@import url('https://fonts.googleapis.com/css?family=News+Cycle');\n\n@font-face {\n  font-family: 'Trade Gothic LT Com Bold Cn';\n  src: url('../fonts/TradeGothicLTCom-BdCn20.eot');\n  src:\n    url('../fonts/TradeGothicLTCom-BdCn20.eot?#iefix') format('embedded-opentype'),\n    url('../fonts/TradeGothicLTCom-BdCn20.woff2') format('woff2'),\n    url('../fonts/TradeGothicLTCom-BdCn20.woff') format('woff'),\n    url('../fonts/TradeGothicLTCom-BdCn20.ttf') format('truetype');\n  font-weight: bold;\n  font-style: normal;\n}\n\n",".accordions {\n  .card {\n    margin-bottom: 0.25rem;\n\n    .card-header {\n      padding: 0;\n      border: none;\n      background-color: transparent;\n      border-radius: 0;\n      box-shadow: none;\n    }\n\n    &:last-child {\n      margin-bottom: 0;\n    }\n\n    h2 {\n      text-transform: none;\n      text-decoration: none;\n      font-size: 1.5rem;\n    }\n\n    button {\n      border: none;\n      box-shadow: 1px 1px 3px rgba($lightgray, 0.25);\n      border-radius: 3px;\n      text-transform: none;\n      text-decoration: none;\n      background-color: $purple;\n      color: $white;\n      width: 100%;\n      display: block;\n      padding: 1rem 1.25rem;\n      text-align: left;\n\n      &.collapsed {\n        background-color: $offwhite;\n        color: $darkgray;\n      }\n    }\n  }\n}\n","//Button specific variables\n$button-primary-color:    $brand-primary;\n$button-seconary-color:   $brand-secondary;\n\n//Button Utility\n.btn--hover